<template>
  <div class="tab-control">
    <div v-for="(item, index) in titles" 
      class="tab-control-item" 
      :class="{active: currentIndex === index}"
      @click="itemClick(index)">
      <span>{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentIndex: 0
    }
  },
  props: {
    titles: {
      type: Array,
      default() {
        return []
      }
    }
  },
  methods: {
    itemClick(index) {
      this.currentIndex = index;
      this.$emit('tabClick', index);
    }
  }
}
</script>

<style scoped>
  .tab-control {
    display: flex;
    font-size: 15px;
    background-color: #fff;
  }
  .tab-control-item {
    flex: 1;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }

  .active span {
    color: var(--color-tint);
    padding: 5px 2px;
    border-bottom: 3px solid var(--color-tint);
  }
</style>
