import category from "../../public/data/category/category.json";
import subcategory from "../../public/data/category/subcategory.json";
import categorydetail from "../../public/data/category/categorydetail.json";

export function getCategory() {
  return new Promise(resolve => {
    resolve(category);
  });
}

export function getSubcategory(maitKey) {
  return new Promise(resolve => {
    resolve(subcategory[maitKey]);
  });
}

export function getCategoryDetail(miniWallkey) {
  return new Promise(resolve => {
    resolve(categorydetail);
  });
}